// vue   
import Vue from 'vue'

// Portal
const globalPortal = {
  scatterAccount: null,
  portalInfoList: [],
}

export default new Vue({
  data () {
    return {
      ...globalPortal
    }
  },
  methods: {
    setScatterAccount(scatterAccount) {
      this.scatterAccount = scatterAccount
    },
    setPortalInfoList(portalInfoList) {
      this.portalInfoList = portalInfoList
    }
  },
  beforeDestroy() {
    this.$off()
  }
})
